"use client";

import { useState, useEffect } from "react";
import { Calendar, Users, LayoutDashboard, LogOut, Share2 } from "lucide-react";
import GeneralTab from "@/components/GeneralTab";
import FriendsTab from "@/components/FriendsTab";
import CalendarTab from "@/components/CalendarTab";
import AuthModal from "@/components/AuthModal";

const TABS = [
    { id: "general", label: "Dashboard", icon: LayoutDashboard },
    { id: "friends", label: "Friends", icon: Users },
    { id: "calendar", label: "Calendar", icon: Calendar },
];

export default function HomePage() {
    const [activeTab, setActiveTab] = useState("general");
    const [isAuthenticated, setIsAuthenticated] = useState(false);
    const [showAuthModal, setShowAuthModal] = useState(false);

    useEffect(() => {
        if (localStorage.getItem("token")) setIsAuthenticated(true);
    }, []);

    const handleLogin = (token: string) => {
        localStorage.setItem("token", token);
        setIsAuthenticated(true);
        setShowAuthModal(false);
    };

    const handleLogout = () => {
        localStorage.removeItem("token");
        setIsAuthenticated(false);
    };

    if (!isAuthenticated) {
        return (
            <div
                style={{
                    minHeight: "100vh",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    background:
                        "radial-gradient(ellipse at 50% 0%, rgba(124,58,237,0.12) 0%, var(--bg-base) 60%)",
                }}
            >
                <div
                    style={{
                        textAlign: "center",
                        maxWidth: "420px",
                        padding: "2rem",
                    }}
                >
                    {/* Logo */}
                    <div
                        style={{
                            display: "inline-flex",
                            alignItems: "center",
                            justifyContent: "center",
                            width: "4rem",
                            height: "4rem",
                            borderRadius: "1.25rem",
                            background: "rgba(124,58,237,0.2)",
                            border: "1px solid rgba(124,58,237,0.4)",
                            marginBottom: "1.5rem",
                        }}
                    >
                        <Share2 size={28} color="var(--purple-400)" />
                    </div>

                    <h1
                        style={{
                            fontSize: "2rem",
                            fontWeight: "700",
                            marginBottom: "0.5rem",
                            color: "var(--text-primary)",
                        }}
                    >
                        SocialAnimal
                    </h1>
                    <p
                        style={{
                            color: "var(--text-secondary)",
                            marginBottom: "2.5rem",
                            lineHeight: "1.6",
                        }}
                    >
                        Open-source calendar sharing for friends.
                        <br />
                        Connect Google, Apple, Proton or any ICS calendar.
                    </p>

                    <div
                        style={{
                            display: "flex",
                            gap: "0.75rem",
                            justifyContent: "center",
                        }}
                    >
                        <button
                            onClick={() => setShowAuthModal(true)}
                            className="button button-primary"
                            style={{
                                padding: "0.75rem 2rem",
                                fontSize: "1rem",
                            }}
                        >
                            Get Started
                        </button>
                    </div>

                    {/* Feature pills */}
                    <div
                        style={{
                            display: "flex",
                            gap: "0.5rem",
                            justifyContent: "center",
                            marginTop: "2.5rem",
                            flexWrap: "wrap",
                        }}
                    >
                        {[
                            "Google Calendar",
                            "Apple Calendar",
                            "Proton",
                            "ICS URL",
                        ].map((f) => (
                            <span key={f} className="badge badge-muted">
                                {f}
                            </span>
                        ))}
                    </div>
                </div>

                {showAuthModal && (
                    <AuthModal
                        onLogin={handleLogin}
                        onClose={() => setShowAuthModal(false)}
                    />
                )}
            </div>
        );
    }

    return (
        <div
            style={{
                minHeight: "100vh",
                display: "flex",
                flexDirection: "column",
            }}
        >
            {/* ‚îÄ‚îÄ Top bar ‚îÄ‚îÄ */}
            <header
                style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "space-between",
                    padding: "0 1.5rem",
                    height: "3.5rem",
                    background: "var(--bg-surface)",
                    borderBottom: "1px solid var(--border)",
                    position: "sticky",
                    top: 0,
                    zIndex: 50,
                }}
            >
                <div
                    style={{
                        display: "flex",
                        alignItems: "center",
                        gap: "0.625rem",
                    }}
                >
                    <div
                        style={{
                            display: "flex",
                            alignItems: "center",
                            justifyContent: "center",
                            width: "1.875rem",
                            height: "1.875rem",
                            borderRadius: "0.5rem",
                            background: "rgba(124,58,237,0.25)",
                            border: "1px solid rgba(124,58,237,0.4)",
                        }}
                    >
                        <Share2 size={14} color="var(--purple-400)" />
                    </div>
                    <span
                        style={{
                            fontWeight: "700",
                            fontSize: "1rem",
                            color: "var(--text-primary)",
                            letterSpacing: "-0.01em",
                        }}
                    >
                        SocialAnimal
                    </span>
                </div>

                <button
                    onClick={handleLogout}
                    className="button button-ghost button-sm"
                    style={{ gap: "0.375rem" }}
                >
                    <LogOut size={14} />
                    Sign out
                </button>
            </header>

            {/* ‚îÄ‚îÄ Tab bar ‚îÄ‚îÄ */}
            <nav className="tab-nav">
                {TABS.map(({ id, label, icon: Icon }) => (
                    <button
                        key={id}
                        onClick={() => setActiveTab(id)}
                        className={`tab-btn${activeTab === id ? " active" : ""}`}
                    >
                        <Icon size={15} />
                        {label}
                    </button>
                ))}
            </nav>

            {/* ‚îÄ‚îÄ Content ‚îÄ‚îÄ */}
            <main
                style={{
                    flex: 1,
                    padding: "1.75rem 1.25rem",
                    maxWidth: "64rem",
                    margin: "0 auto",
                    width: "100%",
                }}
            >
                {activeTab === "general" && <GeneralTab />}
                {activeTab === "friends" && <FriendsTab />}
                {activeTab === "calendar" && <CalendarTab />}
            </main>
        </div>
    );
}
