"use client";

import { useState, useEffect } from "react";
import { Plus, RefreshCw, Trash2, X, Copy, Check, Link } from "lucide-react";
import { env } from "@/lib/env";
import { getCurrentUserId } from "@/lib/utils";

interface CalendarData {
    id: string;
    name: string;
    type: string;
    url?: string;
    events: Array<{ id: string; title: string }>;
    lastSync?: string;
}

interface Friend {
    id: string;
    user1: { id: string; email: string; name?: string };
    user2: { id: string; email: string; name?: string };
    status: string;
}

const CALENDAR_TYPES = [
    {
        value: "ics_url",
        label: "üìÖ ICS URL",
        description: "Import from any public ICS/iCal URL",
    },
    {
        value: "google",
        label: "üîµ Google Calendar",
        description: "Coming soon",
    },
    { value: "apple", label: "üçé Apple Calendar", description: "Coming soon" },
    {
        value: "proton",
        label: "üü£ Proton Calendar",
        description: "Coming soon",
    },
];

export default function GeneralTab() {
    const [calendars, setCalendars] = useState<CalendarData[]>([]);
    const [friends, setFriends] = useState<Friend[]>([]);
    const [isLoading, setIsLoading] = useState(true);

    // Import modal
    const [showImportModal, setShowImportModal] = useState(false);
    const [selectedType, setSelectedType] = useState<string | null>(null);
    const [importName, setImportName] = useState("");
    const [importUrl, setImportUrl] = useState("");
    const [isImporting, setIsImporting] = useState(false);
    const [importError, setImportError] = useState("");

    // Export modal
    const [showExportModal, setShowExportModal] = useState(false);
    const [exportFriendId, setExportFriendId] = useState<string | null>(null);
    const [exportLink, setExportLink] = useState("");
    const [copied, setCopied] = useState(false);

    const [syncingId, setSyncingId] = useState<string | null>(null);

    useEffect(() => {
        fetchCalendars();
        fetchFriends();
    }, []);

    const fetchCalendars = async () => {
        try {
            const token = localStorage.getItem("token");
            const res = await fetch(`${env.API_URL}/api/calendars`, {
                headers: { Authorization: `Bearer ${token}` },
            });
            if (res.ok) setCalendars(await res.json());
        } catch (e) {
            console.error(e);
        } finally {
            setIsLoading(false);
        }
    };

    const fetchFriends = async () => {
        try {
            const token = localStorage.getItem("token");
            const res = await fetch(`${env.API_URL}/api/friends`, {
                headers: { Authorization: `Bearer ${token}` },
            });
            if (res.ok) setFriends(await res.json());
        } catch (e) {
            console.error(e);
        }
    };

    const handleImport = async () => {
        if (!selectedType || !importName) return;
        if (selectedType === "ics_url" && !importUrl) return;
        setIsImporting(true);
        setImportError("");
        try {
            const token = localStorage.getItem("token");
            const res = await fetch(`${env.API_URL}/api/calendars`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    Authorization: `Bearer ${token}`,
                },
                body: JSON.stringify({
                    name: importName,
                    type: selectedType,
                    url: importUrl || null,
                }),
            });
            if (!res.ok)
                throw new Error((await res.json()).error || "Import failed");
            closeImportModal();
            fetchCalendars();
        } catch (err) {
            setImportError(
                err instanceof Error ? err.message : "Import failed",
            );
        } finally {
            setIsImporting(false);
        }
    };

    const closeImportModal = () => {
        setShowImportModal(false);
        setSelectedType(null);
        setImportName("");
        setImportUrl("");
        setImportError("");
    };

    const handleSync = async (calendarId: string) => {
        setSyncingId(calendarId);
        try {
            const token = localStorage.getItem("token");
            await fetch(`${env.API_URL}/api/calendars/${calendarId}/sync`, {
                method: "POST",
                headers: { Authorization: `Bearer ${token}` },
            });
            fetchCalendars();
        } finally {
            setSyncingId(null);
        }
    };

    const handleDelete = async (calendarId: string) => {
        if (!confirm("Delete this calendar and all its events?")) return;
        const token = localStorage.getItem("token");
        await fetch(`${env.API_URL}/api/calendars/${calendarId}`, {
            method: "DELETE",
            headers: { Authorization: `Bearer ${token}` },
        });
        fetchCalendars();
    };

    // Export: generate a live ICS link for a specific friend
    const openExportModal = (friendId?: string) => {
        const token = localStorage.getItem("token");
        const base = `${env.API_URL}/api/ics/my-calendar.ics`;
        const link = friendId
            ? `${base}?friendToken=${btoa(token + ":" + friendId)}`
            : `${base}?token=${token}`;
        setExportLink(link);
        setExportFriendId(friendId ?? null);
        setShowExportModal(true);
        setCopied(false);
    };

    const copyLink = () => {
        navigator.clipboard.writeText(exportLink);
        setCopied(true);
        setTimeout(() => setCopied(false), 2000);
    };

    const acceptedFriends = friends.filter((f) => f.status === "accepted");
    const totalEvents = calendars.reduce(
        (sum, cal) => sum + (cal.events?.length || 0),
        0,
    );
    const currentUserId = getCurrentUserId();

    if (isLoading)
        return (
            <div
                style={{
                    display: "flex",
                    justifyContent: "center",
                    padding: "3rem",
                    color: "rgb(107,114,128)",
                }}
            >
                Loading...
            </div>
        );

    return (
        <div
            style={{ display: "flex", flexDirection: "column", gap: "1.5rem" }}
        >
            {/* ‚îÄ‚îÄ Header ‚îÄ‚îÄ */}
            <div
                style={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                }}
            >
                <h2 style={{ fontSize: "1.5rem", fontWeight: "600" }}>
                    üè† Dashboard
                </h2>
                <div style={{ display: "flex", gap: "0.75rem" }}>
                    <button
                        onClick={() => openExportModal()}
                        className="button button-secondary"
                    >
                        <Link
                            style={{
                                width: "1rem",
                                height: "1rem",
                                marginRight: "0.5rem",
                            }}
                        />
                        Export My Calendar
                    </button>
                    <button
                        onClick={() => setShowImportModal(true)}
                        className="button button-primary"
                    >
                        <Plus
                            style={{
                                width: "1rem",
                                height: "1rem",
                                marginRight: "0.5rem",
                            }}
                        />
                        Import Calendar
                    </button>
                </div>
            </div>

            {/* ‚îÄ‚îÄ Stats ‚îÄ‚îÄ */}
            <div
                style={{
                    display: "grid",
                    gridTemplateColumns: "repeat(auto-fit, minmax(160px, 1fr))",
                    gap: "1rem",
                }}
            >
                {[
                    {
                        label: "Calendars",
                        value: calendars.length,
                        color: "rgb(37,99,235)",
                    },
                    {
                        label: "Total Events",
                        value: totalEvents,
                        color: "rgb(34,197,94)",
                    },
                    {
                        label: "Friends",
                        value: acceptedFriends.length,
                        color: "rgb(168,85,247)",
                    },
                ].map((stat) => (
                    <div
                        key={stat.label}
                        className="card"
                        style={{ textAlign: "center" }}
                    >
                        <div
                            style={{
                                fontSize: "2rem",
                                fontWeight: "700",
                                color: stat.color,
                            }}
                        >
                            {stat.value}
                        </div>
                        <div
                            style={{
                                color: "rgb(107,114,128)",
                                fontSize: "0.875rem",
                            }}
                        >
                            {stat.label}
                        </div>
                    </div>
                ))}
            </div>

            {/* ‚îÄ‚îÄ My Calendars ‚îÄ‚îÄ */}
            <div className="card">
                <h3
                    style={{
                        fontWeight: "600",
                        marginBottom: "1rem",
                        fontSize: "1.1rem",
                    }}
                >
                    üìã My Calendars
                </h3>
                {calendars.length === 0 ? (
                    <div
                        style={{
                            textAlign: "center",
                            padding: "2rem",
                            color: "rgb(107,114,128)",
                        }}
                    >
                        <p style={{ marginBottom: "1rem" }}>No calendars yet</p>
                        <button
                            onClick={() => setShowImportModal(true)}
                            className="button button-primary"
                        >
                            <Plus
                                style={{
                                    width: "1rem",
                                    height: "1rem",
                                    marginRight: "0.5rem",
                                }}
                            />
                            Import Calendar
                        </button>
                    </div>
                ) : (
                    <div
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            gap: "0.75rem",
                        }}
                    >
                        {calendars.map((cal) => (
                            <div
                                key={cal.id}
                                style={{
                                    display: "flex",
                                    justifyContent: "space-between",
                                    alignItems: "center",
                                    padding: "0.75rem",
                                    border: "1px solid rgb(229,231,235)",
                                    borderRadius: "0.5rem",
                                }}
                            >
                                <div>
                                    <div style={{ fontWeight: "600" }}>
                                        {cal.name}
                                    </div>
                                    <div
                                        style={{
                                            fontSize: "0.75rem",
                                            color: "rgb(107,114,128)",
                                            display: "flex",
                                            gap: "0.75rem",
                                            marginTop: "0.2rem",
                                        }}
                                    >
                                        <span className="badge badge-blue">
                                            {cal.type}
                                        </span>
                                        <span>
                                            {cal.events?.length || 0} events
                                        </span>
                                        {cal.lastSync && (
                                            <span>
                                                Synced{" "}
                                                {new Date(
                                                    cal.lastSync,
                                                ).toLocaleString()}
                                            </span>
                                        )}
                                    </div>
                                </div>
                                <div style={{ display: "flex", gap: "0.5rem" }}>
                                    {cal.type === "ics_url" && (
                                        <button
                                            onClick={() => handleSync(cal.id)}
                                            disabled={syncingId === cal.id}
                                            className="button button-secondary"
                                            style={{
                                                padding: "0.375rem 0.75rem",
                                                fontSize: "0.75rem",
                                            }}
                                        >
                                            <RefreshCw
                                                style={{
                                                    width: "0.75rem",
                                                    height: "0.75rem",
                                                    marginRight: "0.25rem",
                                                }}
                                            />
                                            {syncingId === cal.id
                                                ? "Syncing..."
                                                : "Sync"}
                                        </button>
                                    )}
                                    <button
                                        onClick={() => handleDelete(cal.id)}
                                        className="button button-danger"
                                        style={{
                                            padding: "0.375rem 0.75rem",
                                            fontSize: "0.75rem",
                                        }}
                                    >
                                        <Trash2
                                            style={{
                                                width: "0.75rem",
                                                height: "0.75rem",
                                            }}
                                        />
                                    </button>
                                </div>
                            </div>
                        ))}
                    </div>
                )}
            </div>

            {/* ‚îÄ‚îÄ Export Friend Calendars ‚îÄ‚îÄ */}
            <div className="card">
                <h3
                    style={{
                        fontWeight: "600",
                        marginBottom: "0.5rem",
                        fontSize: "1.1rem",
                    }}
                >
                    üîó Share Your Calendar
                </h3>
                <p
                    style={{
                        fontSize: "0.875rem",
                        color: "rgb(107,114,128)",
                        marginBottom: "1rem",
                    }}
                >
                    Generate a live ICS link to give a friend. They can
                    subscribe to it in any calendar app.
                </p>
                {acceptedFriends.length === 0 ? (
                    <p
                        style={{
                            color: "rgb(107,114,128)",
                            fontSize: "0.875rem",
                        }}
                    >
                        Add friends first to share with them.
                    </p>
                ) : (
                    <div
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            gap: "0.5rem",
                        }}
                    >
                        <div
                            style={{
                                display: "flex",
                                justifyContent: "space-between",
                                alignItems: "center",
                                padding: "0.75rem",
                                border: "1px solid rgb(229,231,235)",
                                borderRadius: "0.5rem",
                            }}
                        >
                            <span style={{ fontWeight: "500" }}>
                                üåê Public link (anyone)
                            </span>
                            <button
                                onClick={() => openExportModal()}
                                className="button button-secondary"
                                style={{
                                    fontSize: "0.75rem",
                                    padding: "0.375rem 0.75rem",
                                }}
                            >
                                <Link
                                    style={{
                                        width: "0.75rem",
                                        height: "0.75rem",
                                        marginRight: "0.25rem",
                                    }}
                                />
                                Get Link
                            </button>
                        </div>
                        {acceptedFriends.map((friendship) => {
                            const friend =
                                friendship.user1.id === currentUserId
                                    ? friendship.user2
                                    : friendship.user1;
                            return (
                                <div
                                    key={friendship.id}
                                    style={{
                                        display: "flex",
                                        justifyContent: "space-between",
                                        alignItems: "center",
                                        padding: "0.75rem",
                                        border: "1px solid rgb(229,231,235)",
                                        borderRadius: "0.5rem",
                                    }}
                                >
                                    <span style={{ fontWeight: "500" }}>
                                        {friend.name || friend.email}
                                    </span>
                                    <button
                                        onClick={() =>
                                            openExportModal(friend.id)
                                        }
                                        className="button button-secondary"
                                        style={{
                                            fontSize: "0.75rem",
                                            padding: "0.375rem 0.75rem",
                                        }}
                                    >
                                        <Link
                                            style={{
                                                width: "0.75rem",
                                                height: "0.75rem",
                                                marginRight: "0.25rem",
                                            }}
                                        />
                                        Get Link
                                    </button>
                                </div>
                            );
                        })}
                    </div>
                )}
            </div>

            {/* ‚îÄ‚îÄ Import Modal ‚îÄ‚îÄ */}
            {showImportModal && (
                <div className="modal-overlay" onClick={closeImportModal}>
                    <div
                        className="modal-content"
                        onClick={(e) => e.stopPropagation()}
                        style={{ maxWidth: "480px" }}
                    >
                        <div
                            style={{
                                display: "flex",
                                justifyContent: "space-between",
                                alignItems: "center",
                                marginBottom: "1.5rem",
                            }}
                        >
                            <h3
                                style={{
                                    fontSize: "1.25rem",
                                    fontWeight: "600",
                                }}
                            >
                                Import Calendar
                            </h3>
                            <button
                                onClick={closeImportModal}
                                style={{
                                    background: "none",
                                    border: "none",
                                    cursor: "pointer",
                                }}
                            >
                                <X size={20} />
                            </button>
                        </div>

                        {!selectedType ? (
                            <div
                                style={{
                                    display: "flex",
                                    flexDirection: "column",
                                    gap: "0.75rem",
                                }}
                            >
                                <p
                                    style={{
                                        color: "rgb(107,114,128)",
                                        marginBottom: "0.5rem",
                                    }}
                                >
                                    Choose a source:
                                </p>
                                {CALENDAR_TYPES.map((type) => (
                                    <button
                                        key={type.value}
                                        onClick={() =>
                                            type.value === "ics_url" &&
                                            setSelectedType(type.value)
                                        }
                                        style={{
                                            padding: "1rem",
                                            border: "1px solid rgb(229,231,235)",
                                            borderRadius: "0.5rem",
                                            background: "white",
                                            textAlign: "left",
                                            cursor:
                                                type.value === "ics_url"
                                                    ? "pointer"
                                                    : "not-allowed",
                                            opacity:
                                                type.value === "ics_url"
                                                    ? 1
                                                    : 0.45,
                                        }}
                                    >
                                        <div style={{ fontWeight: "600" }}>
                                            {type.label}
                                        </div>
                                        <div
                                            style={{
                                                fontSize: "0.875rem",
                                                color: "rgb(107,114,128)",
                                            }}
                                        >
                                            {type.description}
                                        </div>
                                    </button>
                                ))}
                            </div>
                        ) : (
                            <div
                                style={{
                                    display: "flex",
                                    flexDirection: "column",
                                    gap: "1rem",
                                }}
                            >
                                <button
                                    onClick={() => setSelectedType(null)}
                                    style={{
                                        background: "none",
                                        border: "none",
                                        cursor: "pointer",
                                        color: "rgb(37,99,235)",
                                        textAlign: "left",
                                        padding: 0,
                                    }}
                                >
                                    ‚Üê Back
                                </button>
                                <div>
                                    <label
                                        style={{
                                            display: "block",
                                            fontWeight: "500",
                                            marginBottom: "0.5rem",
                                        }}
                                    >
                                        Calendar Name
                                    </label>
                                    <input
                                        type="text"
                                        value={importName}
                                        onChange={(e) =>
                                            setImportName(e.target.value)
                                        }
                                        className="input"
                                        placeholder="My Calendar"
                                    />
                                </div>
                                <div>
                                    <label
                                        style={{
                                            display: "block",
                                            fontWeight: "500",
                                            marginBottom: "0.5rem",
                                        }}
                                    >
                                        ICS URL
                                    </label>
                                    <input
                                        type="url"
                                        value={importUrl}
                                        onChange={(e) =>
                                            setImportUrl(e.target.value)
                                        }
                                        className="input"
                                        placeholder="https://example.com/calendar.ics"
                                    />
                                    <p
                                        style={{
                                            fontSize: "0.75rem",
                                            color: "rgb(107,114,128)",
                                            marginTop: "0.25rem",
                                        }}
                                    >
                                        Any public ICS/iCal URL (Google,
                                        Outlook, Fastmail, etc.)
                                    </p>
                                </div>
                                {importError && (
                                    <div
                                        style={{
                                            padding: "0.75rem",
                                            background: "rgb(254,226,226)",
                                            color: "rgb(153,27,27)",
                                            borderRadius: "0.375rem",
                                            fontSize: "0.875rem",
                                        }}
                                    >
                                        {importError}
                                    </div>
                                )}
                                <div
                                    style={{ display: "flex", gap: "0.75rem" }}
                                >
                                    <button
                                        onClick={handleImport}
                                        disabled={
                                            isImporting ||
                                            !importName ||
                                            !importUrl
                                        }
                                        className="button button-primary"
                                        style={{ flex: 1 }}
                                    >
                                        {isImporting
                                            ? "Importing..."
                                            : "Import"}
                                    </button>
                                    <button
                                        onClick={closeImportModal}
                                        className="button button-secondary"
                                    >
                                        Cancel
                                    </button>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
            )}

            {/* ‚îÄ‚îÄ Export Link Modal ‚îÄ‚îÄ */}
            {showExportModal && (
                <div
                    className="modal-overlay"
                    onClick={() => setShowExportModal(false)}
                >
                    <div
                        className="modal-content"
                        onClick={(e) => e.stopPropagation()}
                        style={{ maxWidth: "480px" }}
                    >
                        <div
                            style={{
                                display: "flex",
                                justifyContent: "space-between",
                                alignItems: "center",
                                marginBottom: "1rem",
                            }}
                        >
                            <h3
                                style={{
                                    fontSize: "1.25rem",
                                    fontWeight: "600",
                                }}
                            >
                                üìÖ Your Calendar Link
                            </h3>
                            <button
                                onClick={() => setShowExportModal(false)}
                                style={{
                                    background: "none",
                                    border: "none",
                                    cursor: "pointer",
                                }}
                            >
                                <X size={20} />
                            </button>
                        </div>
                        <p
                            style={{
                                fontSize: "0.875rem",
                                color: "rgb(107,114,128)",
                                marginBottom: "1rem",
                            }}
                        >
                            Subscribe to this URL in any calendar app (Google,
                            Apple, Outlook). It always stays up to date.
                        </p>
                        <div style={{ display: "flex", gap: "0.5rem" }}>
                            <input
                                readOnly
                                value={exportLink}
                                className="input"
                                style={{
                                    fontFamily: "monospace",
                                    fontSize: "0.75rem",
                                }}
                            />
                            <button
                                onClick={copyLink}
                                className="button button-primary"
                                style={{ whiteSpace: "nowrap" }}
                            >
                                {copied ? (
                                    <Check size={16} />
                                ) : (
                                    <Copy size={16} />
                                )}
                            </button>
                        </div>
                        {copied && (
                            <p
                                style={{
                                    color: "rgb(21,128,61)",
                                    fontSize: "0.875rem",
                                    marginTop: "0.5rem",
                                }}
                            >
                                ‚úÖ Copied!
                            </p>
                        )}
                    </div>
                </div>
            )}
        </div>
    );
}
